// =============================================================================
// PLAN PAYMENTS - USDC on Base
// =============================================================================
// Handles USDC payments for plan upgrades using Coinbase/Base infrastructure

import { createPublicClient, http, parseAbi, formatUnits } from 'viem';
import { base } from 'viem/chains';
import crypto from 'crypto';
import { getDb } from '@/db/client';
import { createLogger } from './logger';

const logger = createLogger({ component: 'PlanPayments' });

// =============================================================================
// CONSTANTS
// =============================================================================

// USDC on Base mainnet
export const USDC_ADDRESS = '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913' as const;
export const USDC_DECIMALS = 6;

// NexFlow treasury address - where plan payments are sent
// TODO: Set this to your actual treasury wallet address
export const TREASURY_ADDRESS = process.env.NEXFLOW_TREASURY_ADDRESS || '0x0000000000000000000000000000000000000000';

// Plan prices in USDC (human-readable, will be converted to atomic units)
export const PLAN_PRICES_USDC: Record<string, number> = {
  free: 0,
  starter: 29,    // $29/month
  pro: 99,        // $99/month
  enterprise: 0,  // Contact sales
};

// ERC20 Transfer event ABI
const ERC20_ABI = parseAbi([
  'event Transfer(address indexed from, address indexed to, uint256 value)',
  'function balanceOf(address account) view returns (uint256)',
]);

// =============================================================================
// VIEM CLIENT
// =============================================================================

const publicClient = createPublicClient({
  chain: base,
  transport: http(process.env.BASE_RPC_URL || 'https://mainnet.base.org'),
});

// =============================================================================
// TYPES
// =============================================================================

export interface PlanPaymentIntent {
  id: string;
  accountId: string;
  planId: string;
  amountUsdc: string;        // Human-readable USDC amount
  amountAtomic: string;      // Atomic units (6 decimals)
  recipientAddress: string;
  status: 'pending' | 'paid' | 'verified' | 'expired' | 'failed';
  txHash?: string;
  createdAt: string;
  expiresAt: string;
  verifiedAt?: string;
}

export interface PaymentVerificationResult {
  success: boolean;
  error?: string;
  txHash?: string;
  fromAddress?: string;
  amount?: string;
  blockNumber?: bigint;
}

// =============================================================================
// PAYMENT INTENT FUNCTIONS
// =============================================================================

/**
 * Create a payment intent for a plan upgrade
 */
export async function createPlanPaymentIntent(
  accountId: string,
  planId: string
): Promise<PlanPaymentIntent> {
  const db = getDb();
  
  // Get plan price
  const priceUsdc = PLAN_PRICES_USDC[planId];
  if (priceUsdc === undefined) {
    throw new Error(`Unknown plan: ${planId}`);
  }
  
  if (priceUsdc === 0) {
    throw new Error(`Plan ${planId} is free or requires custom pricing`);
  }
  
  // Validate treasury address
  if (TREASURY_ADDRESS === '0x0000000000000000000000000000000000000000') {
    throw new Error('NEXFLOW_TREASURY_ADDRESS not configured');
  }
  
  const id = `pi_${Date.now()}_${crypto.randomBytes(8).toString('hex')}`;
  const now = new Date();
  const expiresAt = new Date(now.getTime() + 30 * 60 * 1000); // 30 minutes
  
  // Convert to atomic units (USDC has 6 decimals)
  const amountAtomic = (BigInt(priceUsdc) * BigInt(10 ** USDC_DECIMALS)).toString();
  
  const intent: PlanPaymentIntent = {
    id,
    accountId,
    planId,
    amountUsdc: priceUsdc.toString(),
    amountAtomic,
    recipientAddress: TREASURY_ADDRESS,
    status: 'pending',
    createdAt: now.toISOString(),
    expiresAt: expiresAt.toISOString(),
  };
  
  // Store in database
  await db.query(`
    INSERT INTO plan_payment_intents (
      id, account_id, plan_id, amount_usdc, amount_atomic, 
      recipient_address, status, created_at, expires_at
    ) VALUES ($1, $2, $3, $4, $5, $6, $7, $8, $9)
  `, [
    intent.id,
    intent.accountId,
    intent.planId,
    intent.amountUsdc,
    intent.amountAtomic,
    intent.recipientAddress,
    intent.status,
    intent.createdAt,
    intent.expiresAt,
  ]);
  
  logger.info({ intentId: id, accountId, planId, amountUsdc: priceUsdc }, 'Payment intent created');
  
  return intent;
}

/**
 * Get a payment intent by ID
 */
export async function getPaymentIntent(intentId: string): Promise<PlanPaymentIntent | null> {
  const db = getDb();
  
  const result = await db.query<{
    id: string;
    account_id: string;
    plan_id: string;
    amount_usdc: string;
    amount_atomic: string;
    recipient_address: string;
    status: string;
    tx_hash: string | null;
    created_at: string;
    expires_at: string;
    verified_at: string | null;
  }>(`SELECT * FROM plan_payment_intents WHERE id = $1`, [intentId]);
  
  if (result.rows.length === 0) return null;
  
  const row = result.rows[0];
  return {
    id: row.id,
    accountId: row.account_id,
    planId: row.plan_id,
    amountUsdc: row.amount_usdc,
    amountAtomic: row.amount_atomic,
    recipientAddress: row.recipient_address,
    status: row.status as PlanPaymentIntent['status'],
    txHash: row.tx_hash || undefined,
    createdAt: row.created_at,
    expiresAt: row.expires_at,
    verifiedAt: row.verified_at || undefined,
  };
}

// =============================================================================
// PAYMENT VERIFICATION
// =============================================================================

/**
 * Verify a USDC payment on Base
 */
export async function verifyUsdcPayment(
  txHash: string,
  expectedRecipient: string,
  expectedAmountAtomic: string
): Promise<PaymentVerificationResult> {
  try {
    // Get transaction receipt
    const receipt = await publicClient.getTransactionReceipt({
      hash: txHash as `0x${string}`,
    });
    
    if (!receipt) {
      return { success: false, error: 'Transaction not found' };
    }
    
    if (receipt.status !== 'success') {
      return { success: false, error: 'Transaction failed' };
    }
    
    // Look for USDC Transfer event to the treasury
    const transferLogs = receipt.logs.filter(
      (log) => log.address.toLowerCase() === USDC_ADDRESS.toLowerCase()
    );
    
    for (const log of transferLogs) {
      try {
        // Decode the Transfer event
        // Transfer(address indexed from, address indexed to, uint256 value)
        if (log.topics.length >= 3) {
          const to = `0x${log.topics[2]?.slice(-40)}`.toLowerCase();
          const value = BigInt(log.data);
          
          if (
            to === expectedRecipient.toLowerCase() &&
            value >= BigInt(expectedAmountAtomic)
          ) {
            const from = `0x${log.topics[1]?.slice(-40)}`;
            
            return {
              success: true,
              txHash,
              fromAddress: from,
              amount: formatUnits(value, USDC_DECIMALS),
              blockNumber: receipt.blockNumber,
            };
          }
        }
      } catch (e) {
        // Skip logs that don't match Transfer signature
        continue;
      }
    }
    
    return {
      success: false,
      error: 'No matching USDC transfer found in transaction',
    };
  } catch (error: any) {
    logger.error({ error, txHash }, 'Payment verification failed');
    return {
      success: false,
      error: error.message || 'Verification failed',
    };
  }
}

/**
 * Verify and process a plan payment
 */
export async function verifyAndProcessPlanPayment(
  intentId: string,
  txHash: string
): Promise<{ success: boolean; error?: string; newPlan?: string }> {
  const db = getDb();
  
  // Get the payment intent
  const intent = await getPaymentIntent(intentId);
  
  if (!intent) {
    return { success: false, error: 'Payment intent not found' };
  }
  
  if (intent.status === 'verified') {
    return { success: false, error: 'Payment already verified' };
  }
  
  if (intent.status === 'expired' || new Date(intent.expiresAt) < new Date()) {
    return { success: false, error: 'Payment intent expired' };
  }
  
  // Check if tx hash is already used
  const existingResult = await db.query<{ id: string }>(
    `SELECT id FROM plan_payment_intents WHERE tx_hash = $1 AND id != $2`,
    [txHash, intentId]
  );
  
  if (existingResult.rows.length > 0) {
    return { success: false, error: 'Transaction already used for another payment' };
  }
  
  // Verify the on-chain payment
  const verification = await verifyUsdcPayment(
    txHash,
    intent.recipientAddress,
    intent.amountAtomic
  );
  
  if (!verification.success) {
    // Update intent status to failed
    await db.query(
      `UPDATE plan_payment_intents SET status = 'failed', tx_hash = $1 WHERE id = $2`,
      [txHash, intentId]
    );
    return { success: false, error: verification.error };
  }
  
  const now = new Date().toISOString();
  
  // Update payment intent
  await db.query(`
    UPDATE plan_payment_intents 
    SET status = 'verified', tx_hash = $1, verified_at = $2 
    WHERE id = $3
  `, [txHash, now, intentId]);
  
  // Upgrade the plan
  // First, mark current plan as not current
  await db.query(`
    UPDATE account_plans 
    SET is_current = false, expires_at = $1 
    WHERE account_id = $2 AND is_current = true
  `, [now, intent.accountId]);
  
  // Get new plan limits
  const planResult = await db.query<{ daily_limit: number; monthly_limit: number }>(
    `SELECT daily_limit, monthly_limit FROM plans WHERE id = $1`,
    [intent.planId]
  );
  
  if (planResult.rows.length === 0) {
    return { success: false, error: 'Plan not found' };
  }
  
  const plan = planResult.rows[0];
  
  // Create new plan assignment
  await db.query(`
    INSERT INTO account_plans (account_id, plan_id, daily_limit, monthly_limit, started_at, is_current)
    VALUES ($1, $2, $3, $4, $5, true)
  `, [intent.accountId, intent.planId, plan.daily_limit, plan.monthly_limit, now]);
  
  logger.info({
    intentId,
    accountId: intent.accountId,
    planId: intent.planId,
    txHash,
    fromAddress: verification.fromAddress,
  }, 'Plan payment verified and upgraded');
  
  return { success: true, newPlan: intent.planId };
}

// =============================================================================
// UTILITY FUNCTIONS
// =============================================================================

/**
 * Get pending payment intents for an account
 */
export async function getPendingIntents(accountId: string): Promise<PlanPaymentIntent[]> {
  const db = getDb();
  
  const result = await db.query<{
    id: string;
    account_id: string;
    plan_id: string;
    amount_usdc: string;
    amount_atomic: string;
    recipient_address: string;
    status: string;
    created_at: string;
    expires_at: string;
  }>(`
    SELECT * FROM plan_payment_intents 
    WHERE account_id = $1 AND status = 'pending' AND expires_at > NOW()
    ORDER BY created_at DESC
  `, [accountId]);
  
  return result.rows.map(row => ({
    id: row.id,
    accountId: row.account_id,
    planId: row.plan_id,
    amountUsdc: row.amount_usdc,
    amountAtomic: row.amount_atomic,
    recipientAddress: row.recipient_address,
    status: row.status as PlanPaymentIntent['status'],
    createdAt: row.created_at,
    expiresAt: row.expires_at,
  }));
}

/**
 * Expire old payment intents (call from cron job)
 */
export async function expireOldIntents(): Promise<number> {
  const db = getDb();
  
  const result = await db.query(
    `UPDATE plan_payment_intents SET status = 'expired' WHERE status = 'pending' AND expires_at < NOW()`
  );
  
  return result.rowCount || 0;
}

