// =============================================================================
// PLAN UPGRADE PAYMENT API
// =============================================================================
// POST /api/v1/user/upgrade-plan/payment - Create payment intent for plan upgrade
// PUT  /api/v1/user/upgrade-plan/payment - Verify payment and upgrade plan

import { NextRequest, NextResponse } from 'next/server';
import { z } from 'zod';
import { requireAuth } from '@/lib/auth';
import {
  createPlanPaymentIntent,
  verifyAndProcessPlanPayment,
  getPaymentIntent,
  PLAN_PRICES_USDC,
  USDC_ADDRESS,
  TREASURY_ADDRESS,
} from '@/lib/plan-payments';
import { getDb } from '@/db/client';

// =============================================================================
// POST - Create Payment Intent
// =============================================================================

const createIntentSchema = z.object({
  planId: z.enum(['starter', 'pro'], {
    errorMap: () => ({ message: 'Only starter and pro plans require payment' }),
  }),
});

export async function POST(request: NextRequest) {
  // Require authentication
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  try {
    // Parse request
    const body = await request.json();
    const validation = createIntentSchema.safeParse(body);

    if (!validation.success) {
      return NextResponse.json(
        {
          error: 'Validation failed',
          code: 'VALIDATION_ERROR',
          details: validation.error.issues,
        },
        { status: 400 }
      );
    }

    const { planId } = validation.data;

    // Check if treasury is configured
    if (TREASURY_ADDRESS === '0x0000000000000000000000000000000000000000') {
      return NextResponse.json(
        {
          error: 'Payment system not configured',
          code: 'PAYMENT_NOT_CONFIGURED',
          details: 'NEXFLOW_TREASURY_ADDRESS environment variable is not set',
        },
        { status: 503 }
      );
    }

    // Check current plan
    const db = getDb();
    const currentPlanResult = await db.query<{ plan_id: string }>(
      `SELECT plan_id FROM account_plans WHERE account_id = $1 AND is_current = true`,
      [accountId]
    );

    if (currentPlanResult.rows[0]?.plan_id === planId) {
      return NextResponse.json(
        { error: 'You are already on this plan', code: 'ALREADY_ON_PLAN' },
        { status: 400 }
      );
    }

    // Create payment intent
    const intent = await createPlanPaymentIntent(accountId, planId);

    // Return payment instructions
    return NextResponse.json({
      success: true,
      paymentIntent: {
        id: intent.id,
        planId: intent.planId,
        amount: {
          usdc: intent.amountUsdc,
          atomic: intent.amountAtomic,
          display: `$${intent.amountUsdc} USDC`,
        },
        recipient: {
          address: intent.recipientAddress,
          network: 'Base (Chain ID: 8453)',
          token: {
            symbol: 'USDC',
            address: USDC_ADDRESS,
            decimals: 6,
          },
        },
        expiresAt: intent.expiresAt,
        expiresInSeconds: Math.floor(
          (new Date(intent.expiresAt).getTime() - Date.now()) / 1000
        ),
      },
      instructions: {
        step1: 'Send the exact USDC amount to the recipient address on Base network',
        step2: 'Copy the transaction hash after the transfer is confirmed',
        step3: 'Submit the transaction hash using PUT /api/v1/user/upgrade-plan/payment',
        note: 'Payment must be completed within 30 minutes',
      },
    });
  } catch (error: any) {
    console.error('[upgrade-plan/payment] Create error:', error);
    return NextResponse.json(
      {
        error: error.message || 'Failed to create payment intent',
        code: 'INTERNAL_ERROR',
      },
      { status: 500 }
    );
  }
}

// =============================================================================
// PUT - Verify Payment and Upgrade
// =============================================================================

const verifyPaymentSchema = z.object({
  paymentIntentId: z.string().min(1, 'Payment intent ID is required'),
  txHash: z
    .string()
    .regex(/^0x[a-fA-F0-9]{64}$/, 'Invalid transaction hash format'),
});

export async function PUT(request: NextRequest) {
  // Require authentication
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  try {
    // Parse request
    const body = await request.json();
    const validation = verifyPaymentSchema.safeParse(body);

    if (!validation.success) {
      return NextResponse.json(
        {
          error: 'Validation failed',
          code: 'VALIDATION_ERROR',
          details: validation.error.issues,
        },
        { status: 400 }
      );
    }

    const { paymentIntentId, txHash } = validation.data;

    // Verify ownership of intent
    const intent = await getPaymentIntent(paymentIntentId);
    if (!intent) {
      return NextResponse.json(
        { error: 'Payment intent not found', code: 'INTENT_NOT_FOUND' },
        { status: 404 }
      );
    }

    if (intent.accountId !== accountId) {
      return NextResponse.json(
        { error: 'Payment intent belongs to another account', code: 'UNAUTHORIZED' },
        { status: 403 }
      );
    }

    // Verify payment on-chain and upgrade plan
    const result = await verifyAndProcessPlanPayment(paymentIntentId, txHash);

    if (!result.success) {
      return NextResponse.json(
        {
          error: result.error || 'Payment verification failed',
          code: 'PAYMENT_VERIFICATION_FAILED',
        },
        { status: 400 }
      );
    }

    // Get updated plan details
    const db = getDb();
    const planResult = await db.query<{
      name: string;
      daily_limit: number;
      monthly_limit: number;
    }>(`SELECT name, daily_limit, monthly_limit FROM plans WHERE id = $1`, [result.newPlan]);

    const plan = planResult.rows[0];

    return NextResponse.json({
      success: true,
      message: `Successfully upgraded to ${plan?.name || result.newPlan} plan!`,
      plan: {
        id: result.newPlan,
        name: plan?.name,
        dailyLimit: plan?.daily_limit,
        monthlyLimit: plan?.monthly_limit,
      },
      payment: {
        txHash,
        verifiedAt: new Date().toISOString(),
        network: 'Base',
        basescanUrl: `https://basescan.org/tx/${txHash}`,
      },
    });
  } catch (error: any) {
    console.error('[upgrade-plan/payment] Verify error:', error);
    return NextResponse.json(
      {
        error: error.message || 'Failed to verify payment',
        code: 'INTERNAL_ERROR',
      },
      { status: 500 }
    );
  }
}

// =============================================================================
// GET - Get Payment Status
// =============================================================================

export async function GET(request: NextRequest) {
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  const intentId = request.nextUrl.searchParams.get('intentId');

  if (!intentId) {
    // Return all pending intents
    const db = getDb();
    const result = await db.query<{
      id: string;
      plan_id: string;
      amount_usdc: string;
      status: string;
      created_at: string;
      expires_at: string;
    }>(`
      SELECT id, plan_id, amount_usdc, status, created_at, expires_at
      FROM plan_payment_intents
      WHERE account_id = $1
      ORDER BY created_at DESC
      LIMIT 10
    `, [accountId]);

    return NextResponse.json({
      success: true,
      intents: result.rows.map(row => ({
        id: row.id,
        planId: row.plan_id,
        amountUsdc: row.amount_usdc,
        status: row.status,
        createdAt: row.created_at,
        expiresAt: row.expires_at,
      })),
    });
  }

  // Return specific intent
  const intent = await getPaymentIntent(intentId);

  if (!intent) {
    return NextResponse.json(
      { error: 'Payment intent not found', code: 'INTENT_NOT_FOUND' },
      { status: 404 }
    );
  }

  if (intent.accountId !== accountId) {
    return NextResponse.json(
      { error: 'Unauthorized', code: 'UNAUTHORIZED' },
      { status: 403 }
    );
  }

  return NextResponse.json({
    success: true,
    intent: {
      id: intent.id,
      planId: intent.planId,
      amountUsdc: intent.amountUsdc,
      amountAtomic: intent.amountAtomic,
      recipientAddress: intent.recipientAddress,
      status: intent.status,
      txHash: intent.txHash,
      createdAt: intent.createdAt,
      expiresAt: intent.expiresAt,
      verifiedAt: intent.verifiedAt,
    },
  });
}

